.backdrop {
    position:fixed; 
    top:0; 
    left : 0; 
    width : 100%; 
    height:100vh; 
    z-index: 20;
    background-color: rgba(114, 142, 168, 0.507);
}


.modal {
    position: fixed;
    vertical-align: middle;
    top: 10vh;
    overflow-y: visible;
    left: 5%;
    width: 90%;
    min-height: 40vh;
    max-height: 85vh;
    background-color: white;
    padding: 1rem;
    border-radius: 14px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    z-index: 30;
    animation: slide-down 300ms ease-out forwards;
}

.header {
  padding: 5px; 
}

@media (min-width: 768px) {
    .modal {
      width: 40rem;
      left: calc(50% - 20rem);
    }
  }
  
  @keyframes slide-down {
    from {
      opacity: 0;
      transform: translateY(-3rem);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }