.contestpage {
    margin: 0;
    padding: 0;
}

.headbrand {
    display: flex;
    flex-direction: row;

}

.headimg {
    background-image: url("https://img.freepik.com/free-vector/learning-concept-illustration_114360-3454.jpg?t=st=1647936394~exp=1647936994~hmac=38fe73d30144484f97d6c1688add87de9d0997b2fabcaacab15ecfaa3dda0690&w=800");
    width: 100%; 
    height: 95vh;
    background-repeat: no-repeat;
    border-bottom: 2px solid rgb(255, 255, 255);
    box-shadow: 0px 0px 5px 5px rgb(201, 231, 255);
}

.headtext {
    background-color: rgb(242, 255, 252);
    border-radius: 10px;
    color: rgb(49, 0, 95);

    font-family: 'roboto','sans-serif';
    font-size: 35px;
    font-weight: 300;
    letter-spacing: 1px;
    padding: 15px 20px;

    width: 600px;

    transform: translateX(130%);
    margin-top: 10%;
    
    text-align: center;
}

.headtext h1{
    font-weight: 300;
    font-size: 50px;
    padding-left: 0px;
    border-left: 6px solid rgb(0, 209, 174);
}


